@media screen and (max-width: 1279px) {
    div.screen{
        display: none;
    }
    .title{
        position: absolute;
        width: 100%;
        height: 20%;
        left: 0%;
        top: 40%;
        text-align: center;
        font-family: Saysettha OT;
    }
}
@media screen and (min-width: 1280px) {
    div.screen{
        display: absolute;
        position: absolute;
        width: 100%;
        height: 100%;
        background-color: #FFFFFF;
        font-family: Saysettha OT;
    }
    .title{
        display: none;
    }
    .frame{
        display: absolute;
        position: absolute;
        width: 550px;
        height: 350px;
        left: calc(50% - 275px);
        top: calc(50% - 175px);
        opacity: 0.25;
        background-color: #7F8C8D;
        border-radius: 4px;
    }
    .logo{
        display: absolute;
        position: absolute;
        width: 140px;
        height: 140px;
        left: calc(50% - 70px);
        top: calc(50% - 150px);
        background: url(../../images/logo.jpg);
        background-size: 100% 100%;
        border-radius: 4px;
    }
    .txtUsr{
        display: absolute;
        position: absolute;
        width: 280px;
        height: 34px;
        left: calc(50% - 140px);
        top: calc(50% + 10px);
        background-color: #FFFFFF;
        border-radius: 4px;
    }
    .txtPwd{
        display: absolute;
        position: absolute;
        width: 280px;
        height: 34px;
        left: calc(50% - 140px);
        top: calc(50% + 54px);
        background-color: #FFFFFF;
        border-radius: 4px;
    }
    .btnLogin{
        display: absolute;
        position: absolute;
        width: 180px;
        height: 34px;
        left: calc(50% - 90px);
        top: calc(50% + 98px);
        border-radius: 4px;
    }
}